<template>
  <div class="page-index">
    <div class="index-monster-box"
      :style="isShowPlazaBg ? { 'backgroundImage':`url(${require('@/assets/images/ling/ling-tower.png')})` } : { }"
    >
      <div class="index-arrive-box">
        <img v-if="isShow1monster" class='index-1-monster' src='@/assets/images/monster-logo.png' alt='arrived'>
        <div v-if="isShow2Arrived" @click="goToQ2" class="index-2-arrive">
          <div>請前往海洋廣場</div>
          <img class='index-2-arrive-ocean' src='@/assets/images/ocean-plaza.png' alt='arrived'>
          <img class='index-2-arrive-img' src='@/assets/images/had-arrived.png' alt='arrived'>
        </div>
        <div v-if="isShowQ2" class="index-q2-box">
          <div class="index-q2-text">
            你悠閒地走在海洋廣場，天空盤旋著黑鳶飛翔，在雨霧
            濛濛的基隆港，你才看清廣場上歪歪斜斜寫的是「KEELUNG」，穿過
            文字看過去，你看見一名身著西裝、手持雨傘的壯年男子獨自對空中
            比劃著，你原以為他是在練太極拳，但越看越不對勁，他揮動著雨傘
            似乎很吃力，頭上佈滿汗珠，最終男子打開雨傘輕輕一收，這使你不
            禁想起曾在車站看過的神經病。
          </div>
          <div @click="goToQ21" class="index-q2-triangle-box">
            <div class="index-q2-triangle"></div>
          </div>
        </div>
        <div v-if="isShowQ21" class="index-q21-box">
          <div class="index-q21-btn" @click="goTo3">好可怕，趕快離開這裡吧⋯⋯</div>
          <div class="index-q21-btn" @click="goTo4">咦？他在做什麼呢？我去問問看吧。</div>
        </div>
        <div v-if="isShowQ3" class="index-q3-box">
          <img class='index-q3-man' src='@/assets/images/the-man-1.png' alt='arrived'>
          <div class='index-q3-text'>男子擦掉額頭上的汗珠，恢復一般人狀態，自行默默地離開廣場，
          後來一整天你都沒有再遇到他，晚上你便回家了。</div>
          <div @click="goToQ1" class="index-q3-btn">你錯失了加入怪奇組織的機會</div>
        </div>

        <div v-if="isShowQ4" class="index-q4-box">
          <img class='index-q4-man' src='@/assets/images/the-man-1.png' alt='arrived'>
          <div class="index-q4-man-head">館長</div>

          <div v-if="isShowQ4T0" class="index-q4-text-box">
            <div>什麼？剛才我的捕捉行徑都被你看見了！好吧，那我只好告訴你了⋯⋯</div>
            <div @click="goToQ4T1" class="index-q4-triangle-box">
              <div class="index-q4-triangle"></div>
            </div>
          </div>

          <div v-if="isShowQ4T1" class="index-q4-text-box">
            <div>我剛才正在捕捉一隻「怪奇」，他們是一般存在在這世界上的各種
            事物，在長期吸收了負面情緒後化身為怪奇。大概 20 世紀初期，怪奇
            的存在才被證實。世界各地都秘密地成立了相關管理組織，而在台灣
            唯一的組織就是怪奇博物館，收容並且管理這些怪奇。</div>
            <div @click="goToQ4T2" class="index-q4-triangle-box">
              <div class="index-q4-triangle"></div>
            </div>
          </div>

          <div v-if="isShowQ4T2" class="index-q4-text-box">
            <div>但怪奇博物館最近疑似遭到不明攻擊，博物館的結界被破壞，裡面
            的怪奇都逃了出來。目前博物館還一團亂，無法確定逃走的怪奇有哪
            些。其中被認定是危機等級最高的怪奇有 4 個，必須被帶回博物館，
            以免對人類社會造成可怕危害。</div>
            <div @click="goToQ4T3" class="index-q4-triangle-box">
              <div class="index-q4-triangle"></div>
            </div>
          </div>

          <div v-if="isShowQ4T3" class="index-q4-text-box">
            <div>那 4 個怪奇一看到我就知道我的意圖，立馬逃之夭夭，我需要其他
            人的協助。但怪奇的秘密不能隨便透露讓更多人知道，以免造成大眾
            恐慌。既然都被你給撞見了，我想請你協助我一起收服怪奇，你是否
            願意接下這個任務呢？</div>
            <div @click="goToQ41" class="index-q4-triangle-box">
              <div class="index-q4-triangle"></div>
            </div>
          </div>
        </div>

        <div v-if="isShowQ41" class="index-q41-box">
          <img class='index-q41-man' src='@/assets/images/the-man-1.png' alt='arrived'>
          <div class="index-q41-bg">
            <div @click="gotToQ5" class="index-q41-btn">當然願意，不然我點進這個網頁幹嘛？</div>
            <div @click="gotToQ5" class="index-q41-btn">聽起來好像很可怕，但我會努力完成的！</div>
          </div>
        </div>

        <div v-if="isShowQ5" class="index-q5-box">
          <img class='index-q5-man' src='@/assets/images/the-man-1.png' alt='arrived'>
          <div class='index-q5-head'>館長</div>
          <div class='index-q5-text'>很好，收服他們需要的道具包還有筆記都交給你了。他們就潛藏在
          基隆市區各個角落，快將他們帶回博物館吧，基隆人的生命安全就靠你了。</div>
        </div>

      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'PageIndex',
  components: {

  },
  data () {
    return {
      isShow1monster: true,
      isShow2Arrived: false,
      isShowQ2: false,
      cssProps: {
        backgroundImage: `url(${require('@/assets/images/ling/ling-tower.png')})`
      },
      isShowPlazaBg: false,
      isShowQ21: false,
      isShowQ3: false,
      isShowQ4: false,
      isShowQ4T0: false,
      isShowQ4T1: false,
      isShowQ4T2: false,
      isShowQ4T3: false,
      isShowQ41: false,
      isShowQ5: false
    }
  },
  mounted () {
    setTimeout(() => {
      this.isShow1monster = false
      this.isShow2Arrived = true
    }, 2000)
  },
  methods: {
    goToQ2 () {
      this.isShow2Arrived = false
      this.isShowQ2 = true
      this.isShowPlazaBg = true
    },
    goToQ21 () {
      this.isShowQ2 = false
      this.isShowQ21 = true
    },
    goTo3 () {
      this.isShowQ21 = false
      this.isShowQ3 = true
    },
    goToQ1 () {
      this.isShowQ3 = false
      this.isShow1monster = false
      this.isShowPlazaBg = false
      this.isShow2Arrived = true
    },
    goTo4 () {
      this.isShowQ21 = false
      this.isShowQ4 = true
      this.isShowQ4T0 = true
    },
    goToQ4T1 () {
      this.isShowQ4T0 = false
      this.isShowQ4T1 = true
    },
    goToQ4T2 () {
      this.isShowQ4T1 = false
      this.isShowQ4T2 = true
    },
    goToQ4T3 () {
      this.isShowQ4T2 = false
      this.isShowQ4T3 = true
    },
    goToQ41 () {
      this.isShowQ4 = false
      this.isShowQ4T3 = false
      this.isShowQ41 = true
    },
    gotToQ5 () {
      this.isShowQ41 = false
      this.isShowQ5 = true
      this.$store.commit('changeNoteDot', true)
      this.$store.commit('changeHeader', true)
    }
  }
}
</script>
<style lang="scss" scoped>
.page-index {
  background: linear-gradient(#000 70%, #95ff00);
  .index {

    &-monster-box {
      min-height: 100vh;
      display: flex;
      justify-content: center;
      align-items: center;
      background-repeat: no-repeat;
      background-size: cover;
      background-position-x: center;
      background-position-y: center;
    }

    &-1-monster {
      width: 200px;
    }

    &-2-arrive {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      color: #95ff00;

      &-ocean {
        width: 200px;
        margin-top: 50px;
      }

      &-img {
        width: 100px;
        margin-top: 20px;
      }
    }

    &-q2-box {
      height: 200px;
      padding: 20px;
      color: white;
      overflow: scroll;

    }

    &-q2-text {

    }

    &-q2-triangle-box {
      margin-top: 25px;
    }

    &-q2-triangle {
      margin: auto;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 15px 15px 0 15px;
      border-color: white transparent transparent transparent;
    }

    &-q21-box {
      width: 100vw;
      height: 100vh;
      left: 0;
      top: 0;
      background: #000;
      opacity: 0.8;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
    }

    &-q21-btn {
      width: 100%;
      height: 80px;
      line-height: 80px;
      color: white;
      text-align: center;
      background: gray;
      margin-top: 30px;
    }

    &-q3-box {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      position: relative;
    }

    &-q3-man {
      width: 200px;
      position: absolute;
      top: -70px;
    }

    &-q3-text {
      padding: 10px;
      margin-top: 50px;
      color: white;
      z-index: 1;
    }

    &-q3-btn {
      width: 100%;
      height: 80px;
      line-height: 80px;
      color: white;
      text-align: center;
      background: gray;
      margin-top: 30px;
      z-index: 1;
    }

    &-q4-box {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      position: relative;
    }

    &-q4-man {
      width: 300px;
      position: absolute;
    }

    &-q4-man-head {
      margin: 10px 0px;
      padding: 6px;
      background: #95ff00;
      color: black;
      border-radius: 30px;
      z-index: 1;
    }

    &-q4-text-box {
      max-height: 200px;
      padding: 20px;
      color: white;
      overflow: scroll;
      color: black;
      background: #95ff00;
      z-index: 1;
    }

    &-q4-triangle-box {
      margin-top: 25px;
    }

    &-q4-triangle {
      margin: auto;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 15px 15px 0 15px;
      border-color: black transparent transparent transparent;
    }

    &-q41-box {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      position: relative;
    }

    &-q41-man {
      width: 300px;
      position: absolute;
    }

    &-q41-bg {
      position: fixed;
      left: 0;
      top: 0;
      width: 100vw;
      height: 100vh;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      background: #000;
      opacity: 0.9;
    }

    &-q41-btn {
      width: 100%;
      height: 80px;
      line-height: 80px;
      color: white;
      text-align: center;
      background: gray;
      margin-top: 30px;
      z-index: 1;
    }

    &-q5-box {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      position: relative;
    }

    &-q5-man {
      width: 300px;
      padding: 6px;
      position: absolute;
      margin-top: 40px;
    }

    &-q5-head {
      padding: 2px 14px;
      background: #95ff00;
      color: black;
      border-radius: 30px;
      margin: 50px 0px 20px;
      z-index: 1;
    }

    &-q5-text {
      width: 100%;
      padding: 6px 6px 20px 6px;
      background: #95ff00;
      color: black;
      z-index: 1;
    }

  }
}
</style>



